<template>
  <form @submit.prevent="submit">
    <div class="card my-3">
      <div class="card-header">
        <h5 class="card-title">Dados Pessoais</h5>
      </div>
      <div class="card-body">
        <!-- Tipo de Inscrição -->
        <div class="form-group">
          <label for="ipt-tipoinscricao">Tipo de inscrição</label>
          <select id="ipt-tipoinscricao" class="form-control" disabled v-model="tipoinscricao">
            <option :value="1">Principal</option>
            <option :value="2">Secundária</option>
          </select>
        </div>
        <!-- Categoria da Inscrição -->
        <div class="form-group">
          <label for="ipt-categoriainscricao">Categoria da inscrição</label>
          <select id="ipt-categoriainscricao" class="form-control" disabled v-model="categoriainscricao">
            <option :value="1">Pessoa Física</option>
            <option :value="2">Pessoa Jurídica</option>
            <option :value="3">Estagiário</option>
          </select>
        </div>
        <!-- Nome Completo -->
        <div class="form-group">
          <label for="ipt-nomecompleto">Nome completo</label>
          <input id="ipt-nomecompleto" type="text" maxlength="100" class="form-control" disabled v-model="nomecompleto">
        </div>
        <!-- Nome Profissional Abreviado -->
        <div class="form-group">
          <label for="ipt-npa">Nome do profissional abreviado</label>
          <input id="ipt-npa" type="text" placeholder="Não possui" maxlength="70" class="form-control" disabled v-model="npa">
        </div>
        <!-- Data de Inscrição -->
        <div class="form-group">
          <label for="ipt-datainscricao">Data de inscrição no CRECI</label>
          <input id="ipt-datainscricao" type="text" class="form-control" disabled v-model="datainscricao">
        </div>
        <!-- Formação Específica -->
        <div class="form-group">
          <label for="ipt-formacao">Formação específica</label>
          <select id="ipt-formacao" class="form-control" disabled v-model="formacao">
            <option :value="1">Corretor de Imóveis (Lei nº 6.530/1978)</option>
            <option :value="2">Técnico em Transações Imobiliárias</option>
            <option :value="3">Gestor Imobiliário</option>
          </select>
        </div>
        <!-- Data de Nascimento -->
        <div class="form-group">
          <label for="ipt-datanascimento">Data de nascimento</label>
          <input id="ipt-datanascimento" type="text" class="form-control" disabled v-model="datanascimento">
        </div>
        <!-- Sexo -->
        <div class="form-group">
          <label for="ipt-sexo">Sexo</label>
          <select id="ipt-sexo" class="form-control" v-model="sexo">
            <option :value="1">Masculino</option>
            <option :value="2">Feminino</option>
            <option :value="3">Outro</option>
          </select>
        </div>
        <!-- Reservista -->
        <div class="form-group" v-if="sexo === 1">
          <label for="ipt-reservista">Número de Reservista</label>
          <input id="ipt-reservista" type="text" class="form-control" maxlength="15" v-model="reservista">
        </div>
        <!-- Doador de órgãos e tecidos -->
        <div class="form-group">
          <label for="ipt-doador">Doador de órgãos e tecidos</label>
          <select id="ipt-doador" class="form-control" v-model="doador">
            <option :value="1">Positivo</option>
            <option :value="2">Negativo</option>
          </select>
        </div>
        <!-- Nacionalidade -->
        <div class="form-group">
          <label for="ipt-nacionalidade">Nacionalidade</label>
          <input id="ipt-nacionalidade" type="text" maxlength="50" class="form-control" disabled v-model="nacionalidade">
        </div>
        <!-- Naturalidade -->
        <div class="form-group">
          <label for="ipt-naturalidade">Naturalidade (cidade natal)</label>
          <input id="ipt-naturalidade" type="text" maxlength="50" class="form-control" disabled v-model="naturalidade">
        </div>
        <!-- UF da Naturalidade -->
        <div class="form-group">
          <label for="ipt-ufnaturalidade">UF da Naturalidade</label>
          <input id="ipt-ufnaturalidade" type="text" minlength="2" maxlength="2" class="form-control" disabled v-model="ufnaturalidade">
        </div>
        <!-- Nome do Pai -->
        <div class="form-group">
          <label for="ipt-pai">Nome do Pai</label>
          <input id="ipt-pai" type="text" placeholder="Não possui" maxlength="60" class="form-control" v-model="pai">
        </div>
        <!-- Nome da Mãe -->
        <div class="form-group">
          <label for="ipt-mae">Nome da Mãe</label>
          <input id="ipt-mae" type="text" placeholder="Não possui" maxlength="60" class="form-control" v-model="mae">
        </div>
        <!-- Estado Civil -->
        <div class="form-group">
          <label for="ipt-estadocivil">Estado Civil</label>
          <select id="ipt-estadocivil" class="form-control" v-model="estadocivil">
            <option :value="1">Solteiro</option>
            <option :value="2">Casado</option>
            <option :value="3">Viúvo</option>
            <option :value="4">Separado</option>
            <option :value="5">Separado judicialmente</option>
            <option :value="6">Divorciado</option>
            <option :value="8">Desquitado</option>
            <option :value="7">União estável</option>
          </select>
        </div>
        <!-- Escolaridade [Desativado porque o usuário acha que é escolaridade total, não de corretor] -->
        <div class="form-group" v-show="false">
          <label for="ipt-escolaridade">Escolaridade</label>
          <select id="ipt-escolaridade" class="form-control" disabled v-model="escolaridade">
            <option :value="1">Fundamental</option>
            <option :value="2">Médio</option>
            <option :value="3">Superior</option>
            <option :value="4">Pós Graduação</option>
            <option :value="5">Mestrado</option>
            <option :value="6">Doutorado</option>
          </select>
        </div>
        <!-- Tipo Sanguíneo -->
        <div class="form-group">
          <label for="ipt-tiposanguineo">Tipo Sanguíneo</label>
          <select id="ipt-tiposanguineo" class="form-control" v-model="tiposanguineo">
            <option :value="1">Não sabe</option>
            <option :value="2">A+</option>
            <option :value="3">A-</option>
            <option :value="4">B+</option>
            <option :value="5">B-</option>
            <option :value="6">AB+</option>
            <option :value="7">AB-</option>
            <option :value="8">O+</option>
            <option :value="9">O-</option>
          </select>
        </div>
      </div>
      <div class="card-footer">
        <button type="submit" class="btn btn-block btn-primary mx-0">AVANÇAR</button>
        <router-link :to="{name: 'recad-menu'}" class="btn btn-block btn-secondary mx-0">VOLTAR</router-link>
      </div>
    </div>
  </form>
</template>

<script>
  import DateHelper from "../../../helpers/DateHelper";
  export default {
    name: "DadosPessoais",
    data: () => ({
      tipoinscricao: 1,
      categoriainscricao: 1,
      nomecompleto: '',
      npa: '',
      datainscricao: '',
      formacao: 1,
      datanascimento: '',
      sexo: 1,
      reservista: '',
      doador: 1,
      nacionalidade: '',
      naturalidade: '',
      ufnaturalidade: '',
      pai: '',
      mae: '',
      estadocivil: 1,
      escolaridade: 1,
      tiposanguineo: 1
    }),
    created() {
      //Alimentar campos
      if (!sessionStorage.getItem('recad_dadospessoais')) {
        //Carrega da Store
        this.tipoinscricao = this.$store.state.tipo;
        this.categoriainscricao = this.$store.state.categoria;
        this.nomecompleto = this.$store.state.nome;
        this.npa = this.$store.state.npa ? this.$store.state.npa : '';
        this.datainscricao = DateHelper.date_SQLparaBR(this.$store.state.datainscricao);
        this.formacao = this.$store.state.formacao;
        this.datanascimento = DateHelper.date_SQLparaBR(this.$store.state.datanascimento);
        this.sexo = this.$store.state.sexo;
        if (this.$store.state.reservista) this.reservista = this.$store.state.reservista;
        this.doador = this.$store.state.doador;
        this.nacionalidade = this.$store.state.nacionalidade;
        this.naturalidade = this.$store.state.naturalidade;
        this.ufnaturalidade = this.$store.state.naturalidade_uf;
        this.pai = this.$store.state.pai;
        this.mae = this.$store.state.mae;
        this.estadocivil = this.$store.state.estadocivil;
        this.escolaridade = this.$store.state.formacao === 3 ? 3 : 2; //tentar pegar este dado da base do cofeci
        //tipo sanguineo... pegar este dado da base do cofeci
      } else {
        //Carrega da sessionStorage
        const dados = JSON.parse(sessionStorage.getItem('recad_dadospessoais'));
        this.tipoinscricao = dados.tipoinscricao;
        this.categoriainscricao = dados.categoriainscricao;
        this.nomecompleto = dados.nomecompleto;
        this.npa = dados.npa;
        this.datainscricao = dados.datainscricao;
        this.formacao = dados.formacao;
        this.datanascimento = dados.datanascimento;
        this.sexo = dados.sexo;
        this.reservista = dados.reservista;
        this.doador = dados.doador;
        this.nacionalidade = dados.nacionalidade;
        this.naturalidade = dados.naturalidade;
        this.ufnaturalidade = dados.ufnaturalidade;
        this.pai = dados.pai;
        this.mae = dados.mae;
        this.estadocivil = dados.estadocivil;
        this.escolaridade = dados.escolaridade;
        this.tiposanguineo = dados.tiposanguineo;
      }
    },
    beforeRouteLeave (to, from, next) {
      sessionStorage.setItem('recad_dadospessoais', JSON.stringify({
        tipoinscricao: this.tipoinscricao,
        categoriainscricao: this.categoriainscricao,
        nomecompleto: this.nomecompleto,
        npa: this.npa,
        datainscricao: this.datainscricao,
        formacao: this.formacao,
        datanascimento: this.datanascimento,
        sexo: this.sexo,
        reservista: this.reservista,
        doador: this.doador,
        nacionalidade: this.nacionalidade,
        naturalidade: this.naturalidade,
        ufnaturalidade: this.ufnaturalidade,
        pai: this.pai,
        mae: this.mae,
        estadocivil: this.estadocivil,
        escolaridade: this.escolaridade,
        tiposanguineo: this.tiposanguineo
      }));
      next();
    },
    methods: {
      submit() {
        this.$router.push({name: 'recad-form-documentacao'});
      }
    }
  }
</script>
